<template><section><h1>GeometryInstance</h1>
<p>The <code v-pre>vc-instance-geometry</code> component is used to load a geometry. Geometry instancing allows one Geometry object to be positions in several different locations and colored uniquely. For example, one BoxGeometry can be instanced several times, each with a different modelMatrix to change its position, rotation, and scale.</p>
<h2>Example</h2>
<h3>Load GeometryInstance with <code v-pre>vc-instance-geometry</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-primitive :appearance="appearance">
          <vc-instance-geometry id="top" :geometry="geometry" :color="color" :attributes="attributes" :modelMatrix="modelMatrix1"></vc-instance-geometry>
          <vc-instance-geometry id="bottom" :geometry="geometry" :color="color" :attributes="attributes" :modelMatrix="modelMatrix2"></vc-instance-geometry>
        </vc-primitive>
        <vc-primitive :appearance="appearance2" :geometryInstances.sync="geometryInstances">
          <vc-instance-geometry>
            <vc-geometry-rectangle :rectangle="rectangle"></vc-geometry-rectangle>
          </vc-instance-geometry>
          <vc-instance-geometry :geometry.sync="geometry2">
            <vc-geometry-polygon :polygonHierarchy="polygonHierarchy" :height="height"></vc-geometry-polygon>
          </vc-instance-geometry>
        </vc-primitive>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
